@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&family=Source+Serif+Pro:wght@700&family=Varela+Round&display=swap);
:root{--main-font: 'Varela Round', sans-serif;--secondary-font: 'Source Serif Pro', serif;--thrid-font: 'Great Vibes', cursive}.Open{opacity:0;transition:all 0.5s ease-in-out}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:hidden}

video{object-fit:cover;width:100vw;height:100vh;position:fixed;top:0;left:0}

.Footer{position:fixed;display:flex;justify-content:center;bottom:0;margin:auto;z-index:1;height:100px;width:100%;transition:all 0.5s ease-in-out;opacity:1}.Footer .Creator{background-color:unset;color:lightgrey;border:unset;font-size:40px;font-weight:500;font-family:var(--thrid-font)}.Footer .Creator:focus{outline:none}

.Links{position:fixed;display:flex;flex-wrap:wrap;max-width:50vw;top:30px;left:30px;margin:auto;z-index:2}

.Link{display:absolute;justify-content:center;align-items:center;height:80px;width:80px;transform:rotate(45deg);background-color:rgba(255,255,255,0.3);border-radius:10px;margin:30px;text-decoration:none;color:black;text-align:center;font-family:var(--secondary-font)}.Link .LinkName{display:relative;transform:rotate(-45deg);font-size:60px;line-height:unset;top:0;margin:auto;z-index:3;transform-origin:60% 50%}.Link:hover:after{content:attr(data-before);position:fixed;width:100%;height:100%;top:0;left:0;right:0;bottom:0;border-radius:10px;color:rgba(255,255,255,0.7);background-color:rgba(0,0,0,0.3);z-index:5;cursor:pointer}

.Weather{position:fixed;display:flex;align-items:center;justify-content:center;flex-direction:column;max-width:50vw;top:60px;right:60px;margin:auto;z-index:2;padding:40px;box-sizing:border-box;background-color:rgba(255,255,255,0.3);border-radius:10px;font-weight:600;font-family:var(--main-font)}.Weather .Weather-station{font-size:40px;margin:0}.Weather .Weather-condition{margin-top:0;margin-bottom:20px}.Weather .Weather-time{margin:0}.Weather .Weather-source{margin:0;color:white}.Weather .Weather-icon .Svg{width:200px;height:200px}.Loading{height:300px}

.Settings{opacity:1;position:fixed;display:flex;flex-direction:column;bottom:10px;border-radius:10px;right:0;left:0;margin:auto;z-index:1;width:80%;background-color:rgba(255,255,255,0.3);padding:10px;box-sizing:border-box;z-index:3;transition:all 0.5s ease-in-out}.Settings .Close{position:absolute;right:7px;top:0px;margin:0 5px;width:-webkit-min-content;width:-moz-min-content;width:min-content;font-weight:600;font-size:25px;border:unset;border-radius:10px;padding:5px 15px;background-color:unset;color:white}.Settings .Close:focus{outline:none}.Settings .Options{display:flex}.Settings .Inputs{display:flex;flex-direction:column;margin:0px 10px}.Settings .Input-error{text-align:center;color:darkred;background-color:rgba(255,255,255,0.3);border-radius:10px}.Settings label{color:white;font-family:var(--main-font)}.Settings .Input,.Settings .Select{border:none;border-radius:10px;font-size:16px;margin:5px 0;outline-width:0;padding:2px 8px;min-width:200px;box-sizing:border-box}.Settings .DynamicList{display:flex;justify-content:space-between;align-items:center;color:white;font-size:20px}.Settings .DynamicList p{margin:unset}.Settings .DynamicList .DelleteLink{height:-webkit-min-content;height:-moz-min-content;height:min-content;border:none;border-radius:100%;background-color:unset;color:white;font-size:20px;font-weight:bold;margin-left:10px;cursor:pointer}.Settings .DynamicList .DelleteLink:focus{outline:none}.Settings .Contacts{color:white;display:flex;align-items:center;justify-content:center;font-family:var(--main-font);text-decoration:none}.Settings .Contacts:hover{cursor:pointer}

